export async function AddAddress(userdata) {
  console.log("Add Address API Called")
  return new Promise(async (resolve) => {
    const response = await fetch("/address/add", {
      method: "POST",
      body: JSON.stringify(userdata),
      headers: { "content-type": "application/json" },
    });

    const data = response.json();
    resolve({ data });
  });
}

export async function FetchAddress(userdata) {
  return new Promise(async (resolve) => {
    const response = await fetch("/address/Address" ,{
      method:'POST',
      body:JSON.stringify(userdata),
      headers:{"content-type":"application/json"}
    });  
    const data = response.json();
    resolve({ data });
  });
}

export async function RemoveAddress(userdata){
   return new Promise(async (resolve)=>{
      const response = await fetch(`/address/`+ userdata,{
         method:"DELETE"
      })
      const data = response.json();
      resolve({data})
   })
}
