import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { AddToCart, FetchCart, RemoveCart, UpdateCart } from "./CartAPI";

const initialState = {
  status: "idle",
  cart: [],
};

export const AddToCArtAsync = createAsyncThunk("Cart/AddToCart", async (userdata) => {
  const response = await AddToCart(userdata);
  return response.data;
});

export const FetchCartAsync = createAsyncThunk('Cart/FetchCart', async (userdata)=>{
   const response = await FetchCart(userdata);
   return response.data;
})

export const UpdateCartAsync = createAsyncThunk('Cart/UpdateCart', async (userdata)=>{
   const response = await UpdateCart(userdata);
   return response.data;
})

export const RemoveCartAsync = createAsyncThunk("/Cart/RemoveCart", async (userdata)=>{
   const response = await RemoveCart(userdata)
   return response.data;
})


export const CartSlice = createSlice({
   name:"Cart",
   initialState,
   reducers:{},
   extraReducers:(builder)=>{
      builder
      .addCase(AddToCArtAsync.pending,(state)=>{
         state.status = 'pending';
      })
      .addCase(AddToCArtAsync.fulfilled, (state,actions)=>{
         state.status ="idle";
         state.cart.push(actions.payload);
      })
      .addCase(FetchCartAsync.pending,(state)=>{
         state.status="pending";
      })
      .addCase(FetchCartAsync.fulfilled,(state,actions)=>{
         state.status="idle";
         state.cart =actions.payload;
      })
      .addCase(UpdateCartAsync.pending,(state)=>{
         state.status ="pending";
      })
      .addCase(UpdateCartAsync.fulfilled,(state,actions)=>{
         state.status ='idle';
         const index = state.cart.findIndex(item=>item._id===actions.payload._id);
         state.cart[index] = actions.payload;
      })
      .addCase(RemoveCartAsync.pending,(state)=>{
         state.status="pending";
      })
      .addCase(RemoveCartAsync.fulfilled,(state,actions)=>{
         state.status ="idle";
         const index = state.cart.findIndex(item=>item._id === actions.payload._id)
         state.cart.splice(index,1)
      })
   }
})

export const CartData = (state) => state.Cart.cart;

export default CartSlice.reducer;