import React, { useState } from 'react'
import Address_Component from '../Small_Components/Address_Component'
import { useDispatch, useSelector } from 'react-redux'
import { loggeduser } from '../Authentication/AuthSlice'
import { AddAddressAsync, Address } from '../Features/Address/AddressSlice'

export default function () {
   const [flag ,SetFlag] = useState(true)
   const [name,setName] =useState("")
   const [Contact,setContact] =useState("")
   const [street,setStreet] =useState("")
   const [state,setState] =useState("")
   const [district,setDistrict] =useState("")
   const [zip,setZip] =useState("")
   const dispatch = useDispatch("")
   const user = useSelector(loggeduser)

   const Addresses = useSelector(Address)
  //  const Addresses = [ {
  //     Name: "Sameer kumar",
  //     Primary_Contact_Number: "9334218721",
  //     Secondary_Contact_Number: "9334218721",
  //     Street: "P C College",
  //     District:"Buxar",
  //     State:"Bihar",
  //     Pincode: "802103",
  //   },
  //   {
  //     Name: "Sameer kumar",
  //     Primary_Contact_Number: "9334218721",
  //     Secondary_Contact_Number: "9334218721",
  //     Street: "P C College",
  //     District:"Buxar",
  //     State:"Bihar",
  //     Pincode: "802103",
  //   },
  //   {
  //     Name: "Sameer kumar",
  //     Primary_Contact_Number: "9334218721",
  //     Secondary_Contact_Number: "9334218721",
  //     Street: "P C College",
  //     District:"Buxar",
  //     State:"Bihar",
  //     Pincode: "802103",
  //   }]


    const handleClick =(e)=>{
      console.log("Working")
      
      e.preventDefault();
      if(!name){
        alert("name is required")
        return ;
      }

      if(!Contact){
        alert("Contact Detail is required")
        return ;
      }
      if(!street){
        alert("Street is required")
        return ;
      }
      if(!state){
        alert("State is required")
        return ;
      }

      if(!district){
        alert("District is required")
        return ;
      }
      if(!zip){
        alert("Zip Code is required")
        return ;
      }

      dispatch(AddAddressAsync({user:user.id,street:street,District:district,Zipcode:zip,State:state,primary_contact_number:Contact}))
      setName(" ")
      SetFlag(!flag);
      
      return ;
  
      
    }
   
   
   
  return (
    <div className=' bg-light-orange-100 px-16 py-16'>
       <div style={{display:flag?"block":"none"}}>
      <div className='flex justify-end items-center mb-8 sansita-regular text-gray-green text-xl'>
      <svg width="34" height="34" viewBox="0 0 34 34" fill="none" xmlns="http://www.w3.org/2000/svg">
<path opacity="0.4" d="M9.5 17H26.5" stroke="black" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
<path d="M18 25.5V8.5" stroke="black" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
</svg>
<p onClick={()=>SetFlag(!flag)} className='cursor-pointer'> Add new Address</p>
      </div>
     
      { Addresses == ""?<p className='grid grid-cols-3 justify-center items-center '>No Address</p>:
         Addresses.map((data)=>{
            return (
            <Address_Component data={data}/>
            )
         })
      }
      </div>
<form style={{ display: !flag ? "block" : "none" }}
          onSubmit={(e) => handleClick(e)}

          id="MyFrom">
      <div className="border-b border-gray-900/10 pb-12">
          <h2 className="text-base font-semibold leading-7 text-gray-900">Personal Information</h2>
          <p className="mt-1 text-sm leading-6 text-gray-600">Use a permanent address where you can receive mail.</p>

          <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
            <div className="sm:col-span-3">
              <label htmlFor="first-name" className="block text-sm font-medium leading-6 text-gray-900">
                First name
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="first-name"
                  id="first-name"
                  onChange={(e)=>setName(e.target.value)}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

           

            <div className="sm:col-span-4">
              <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
              Primary  Contact Number
              </label>
              <div className="mt-2">
                <input
                  id="email"
                  name="email"
                  type="tel"
                  onChange={(e)=>setContact(e.target.value)}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-3">
              <label htmlFor="country" className="block text-sm font-medium leading-6 text-gray-900">
                Country
              </label>
              <div className="mt-2">
                <select
                  id="country"
                  name="country"
                  autoComplete="country-name"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:max-w-xs sm:text-sm sm:leading-6"
                >
                  <option>India</option>
                  <option>Canada</option>
                  <option>Mexico</option>
                </select>
              </div>
            </div>

            <div className="col-span-full">
              <label htmlFor="street-address" className="block text-sm font-medium leading-6 text-gray-900">
                Street address
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="street-address"
                  id="street-address"
                  onChange={(e)=>setStreet(e.target.value)}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-2 sm:col-start-1">
              <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
                City
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="city"
                  id="city"
                  onChange={(e)=>setDistrict(e.target.value)}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-2">
              <label htmlFor="region" className="block text-sm font-medium leading-6 text-gray-900">
                State / Province
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="region"
                  id="region"
                  onChange={(e)=>setState(e.target.value)}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-2">
              <label htmlFor="postal-code" className="block text-sm font-medium leading-6 text-gray-900">
                ZIP / Postal code
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="postal-code"
                  id="postal-code"
                  onChange={(e)=>setZip(e.target.value)}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
          </div>
          <div className="mt-6 flex items-center justify-end gap-x-6">
        <button type="button" className="text-sm font-semibold leading-6 text-gray-900" onClick={()=> SetFlag(!flag)}>
          Cancel
        </button>
        <button
          type='submit'
          className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
        >
          Save
        </button>
      </div>
        </div>
        </form>
       
    </div>
  )
}
