import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { AddAddress, FetchAddress, RemoveAddress } from "./AddressAPI";

const initialState = {
  status: "idle",
  address: [],
};

export const AddAddressAsync = createAsyncThunk(
  "Address/AddAdress",
  async (userdata) => {
    const response = await AddAddress(userdata);
    return response.data;
  }
);

export const FetchAddressAsync = createAsyncThunk(
  "Address/FetchAddress",
  async (userdata) => {
    const response = await FetchAddress(userdata);
    return response.data;
  },
);

export const RemoveAddressAsync = createAsyncThunk('Address/RemoveAddress', async (userdata)=>{
  const response = await RemoveAddress(userdata);
  return response.data;
})

export const AddressSlice = createSlice({
  name: "Address",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(AddAddressAsync.pending, (state) => {
        state.status = "pending";
      })
      .addCase(AddAddressAsync.fulfilled, (state, actions) => {
        state.status = "idle";
        state.address.push(actions.payload);
      })
      .addCase(FetchAddressAsync.pending, (state) => {
        state.status = "pending";
      })
      .addCase(FetchAddressAsync.fulfilled, (state, actions) => {
        state.status = "idle";
        state.address = actions.payload;
      })
      .addCase(RemoveAddressAsync.pending,(state)=>{
        state.status = "pending";
      })
      .addCase(RemoveAddressAsync.fulfilled,(state,actions)=>{
        state.status = 'idle';
        const index = state.address.findIndex(item=>item._id === actions.payload._id)
        state.address.splice(index,1)
      });
  },
});

export const Address = (state)=>state.Address.address;

export default AddressSlice.reducer;