import React, { useEffect, useState } from "react";
import '../css/main.css'

import NavBar from "../Naviagations/NavBar";
import { useDispatch, useSelector } from "react-redux";
import { CartData, RemoveCartAsync, UpdateCartAsync } from "../Features/Cart/CartSlice";
import { useNavigate } from "react-router-dom";

export default function Cart() {
  const products = useSelector(CartData)
  const dispatch = useDispatch()
  const navigate = useNavigate()
  let totalitem= 0;
  let totalprice = 0;

  for(let i=0;i<products.length;i++){
    totalitem = totalitem + products[i].quantity;
    totalprice = totalprice + Math.round(products[i].product.price/100*products[i].product.discountpercent)*products[0].quantity;
  }

  const  IncreaseQuantity = (data) =>{
    dispatch(UpdateCartAsync({id:data._id,quantity:data.quantity+1}))

  }

  const DecreaseQuantity = (data)=>{
    if(data.quantity ===1){
      alert("You can't Decrease qunatity")
      return ;
    }

    dispatch(UpdateCartAsync({id:data._id,quantity:data.quantity-1}))

  }
  const HandleOrder = () =>{

    navigate('/order')

  }



  


  return (
    <div>
      <NavBar />

      <div className="bg-light-orange-100 min-h-[70vh] px-96 py-20">

        {
          products.map((data)=>{
            const arr = [];

            for(let i=0;i<data.product.rating;i++){
               arr.push(1);
            }
            return (
             <div className="grid grid-cols-2">
              <img src={data.product.thumbnail} className="w-60 py-4"/>
              <div className="py-2  text-gray-green">
                <p className="chathura-regular  text-3xl ">{data.product.title}</p>
                <div className='pt-1'>

                {
               arr.map(()=>
                  <span className='text  text-dark-orange-1000 text-xl'>&#9733; </span>
               )
            }
          <span className='charmonman-regular text-sm text-dark-orange-1000'>{data.product.reviews} reviews</span>
          </div>
          <div className='text-lg'>
          <span className='pr-2 line-through text-gray-green sansita-regular'> &#x20B9;{data.product.price}</span>
            <span className='pr-2 text-gray-green sansita-regular'> &#x20B9;{Math.round(data.product.price - (data.product.price*data.product.discountpercent/100))}</span>
            <span className='text-dark-orange-1000 charmonman-regular text-sm'>{data.product.discountpercent}% Save</span>
         </div>
         <p className="rufina-regular pt-2">{data.product.discription.slice(0,60)}</p>
         <div className="flex gap-10 pt-3 ">
          <div className="flex bg-button-color rounded-md px-2 py-1">
            <p className="bg-white pb-1 px-2 rounded cursor-pointer"  onClick={()=> IncreaseQuantity({_id:data._id,quantity:data.quantity})} >+</p> <p className="px-2">{data.quantity}</p> <p className="bg-white pb-1 px-3 rounded cursor-pointer"  onClick={()=> DecreaseQuantity({_id:data._id,quantity:data.quantity})} >-</p>
          </div>
           <button type="button" onClick={()=>dispatch(RemoveCartAsync(data._id))} className="px-6 shadow-2xl py-1 bg-button-color text-gray-green rounded-full sansita-regular ">Remove</button>
          </div>
              </div>
             </div>
            )
          })
        }

      </div>

      <div className="flex h-40 justify-around items-center">

        <div>
          <div className=" flex athiti-bold  text-lg text-gray-green">
            <p>Total Items:-  &nbsp; </p> <p>{totalitem}</p>
          </div>
          <div className=" flex athiti-bold  text-lg text-gray-green">
            <p>Total Price:-  &nbsp; </p> <p>{totalprice}</p>
          </div>
        </div>
        <button className="bg-gray-green py-2 h-12 px-5 rounded-full text-white sansita-bold" onClick={HandleOrder}>Checkout</button>
      </div>
      
    </div>
  );
}
