import React, { useState } from "react";
import NavBar from "../Naviagations/NavBar";
import "../css/main.css";
import fast from "../visules/imgs/fast.png";
import dilivery from "../visules/imgs/payment.png";
import payment from "../visules/imgs/dilivery.png";
import Fotter from "../Naviagations/Fotter";
import { useSelector } from "react-redux";
import {
  SelectedProduct,
  selectsimilarProduct,
} from "../Features/Product/ProductSlice";

export default function Product_details() {
  const [selectedimg, setSelectedimg] = useState(0);
  const selectedProduct = useSelector(SelectedProduct);
  const products = useSelector(selectsimilarProduct);

  const features = [
    {
      icon: payment,
      title: "Secure Transitction",
    },
    {
      icon: fast,
      title: "Payment Method",
    },
    {
      icon: dilivery,
      title: "Secure Transitction",
    },
  ];

  const arr = [];

  for (let i = 0; i < selectedProduct.rating; i++) {
    arr.push(1);
  }
  return (
    <div>
      <NavBar />
      <div className="pb-20">
        <div className="grid grid-cols-7 px-20 py-20 bg-light-orange-100">
          <div>
            {selectedProduct.images.map((img, index) => {
              return (
                <img
                  src={img}
                  className="pb-2 cursor-pointer"
                  onClick={(e) => setSelectedimg(index)}
                />
              );
            })}
          </div>
          <div className="col-span-4 flex justify-center items-center px-20">
            <img
              src={selectedProduct.images[selectedimg]}
              className="hover:scale-125 hover:cursor-zoom-in w-[800px]"
            />
          </div>
          <div className="col-span-2 flex flex-col justify-center">
            <p className="text-gray-green chathura-bold text-3xl">
              {selectedProduct.title}
            </p>
            <div className="pt-4">
              {arr.map(() => (
                <span className="text  text-dark-orange-1000 text-xl">
                  &#9733;{" "}
                </span>
              ))}
              <span className="charmonman-regular text-2xl text-dark-orange-1000">
                {selectedProduct.reviews} reviews
              </span>
            </div>

            <p className="rufina-regular text-gray-green pt-4">
              {selectedProduct.discription}
            </p>

            <div>
              <div className="flex">
                {features.map((data) => {
                  return (
                    <div className="flex flex-col justify-center items-center px-2 py-4 cursor-pointer">
                      <img src={data.icon} />
                      <p className="text-gray-green text-sm sansita-bold">
                        {data.title}
                      </p>
                    </div>
                  );
                })}
              </div>
              <div className="pt-4">
                <span className="l line-through pr-2 text-gray-green sansita-regular">
                  &#x20B9; {selectedProduct.price}
                </span>
                <span className="pr-2 text-gray-green sansita-regular">
                  {" "}
                  &#x20B9;
                  {Math.round(
                    selectedProduct.price -
                      (selectedProduct.price *
                        selectedProduct.discountpercent) /
                        100
                  )}
                </span>
                <span className="text-dark-orange-1000 charmonman-regular text-lg">
                  {selectedProduct.discountpercent}% Save
                </span>
              </div>
              <div className="flex justify-center gap-10 pt-4">
                <button className="text text-white-shoadow px-5 py-2 shadow-2xl bg-gray-green rounded-full hover:text-gray-green hover:bg-white-shoadow  text-base sansita-regular">
                  Shop Now
                </button>
                <button className="text hover:text-white-shoadow px-4 py-2 hover:bg-gray-green rounded-full text-gray-green  shadow-2xl bg-white-shoadow text-base sansita-regular">
                  Add To Cart
                </button>
              </div>
            </div>
          </div>
        </div>
        <div>
          <p className="flex justify-center items-center px-20 py-20 text-3xl sansita-regular-italic ">
            Jewellery related to this
          </p>
          <div className="grid grid-cols-4 gap-16 px-32">
            {products.map((data) => {
              const arr = [];

              for (let i = 0; i < data.rating; i++) {
                arr.push(1);
              }
              return (
                <div>
                  <img src={data.thumbnail} />
                  <p className="pt-4 rufina-regular text-sm text-gray-green">
                    {data.title}
                  </p>
                  <div className="pt-2">
                    {arr.map(() => (
                      <span className="text  text-dark-orange-1000 text-xl">
                        &#9733;{" "}
                      </span>
                    ))}
                    <span className="charmonman-regular text-dark-orange-1000">
                      {data.reviews} reviews
                    </span>
                  </div>
                  <div className="">
                    <span className="l line-through pr-2 text-gray-green">
                      &#x20B9; {data.price}
                    </span>
                    <span className="pr-2 text-gray-green sansita-regular">
                      {" "}
                      &#x20B9;
                      {Math.round(
                        data.price - (data.price * data.discountpercent) / 100
                      )}
                    </span>
                    <span className="text-dark-orange-1000 charmonman-regular text-sm">
                      {data.discountpercent}% Save
                    </span>
                  </div>
                  <div className="flex justify-center gap-10 pt-4">
                    <button className="text text-white-shoadow px-5 py-2 shadow-2xl bg-gray-green rounded-full hover:text-gray-green hover:bg-white-shoadow  text-base sansita-regular">
                      Shop Now
                    </button>
                    <button className="text hover:text-white-shoadow px-4 py-2 hover:bg-gray-green rounded-full text-gray-green  shadow-2xl bg-white-shoadow text-base sansita-regular">
                      Add To Cart
                    </button>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
      <Fotter />
    </div>
  );
}
