import React, { useEffect } from 'react'
import '../css/main.css'
import {useSelector, useDispatch} from 'react-redux'
import NavBar from '../Naviagations/NavBar'
import {useNavigate} from 'react-router-dom';
import { Fragment, useState } from 'react'
import {Menu, Transition } from '@headlessui/react'
import { ChevronDownIcon} from '@heroicons/react/20/solid'
import { ChevronLeftIcon, ChevronRightIcon } from '@heroicons/react/20/solid'
import { FetchProductAsync, FetchProductbyCategoryAsync, FetchProductbyIdAsync, ToatalProducts, TotalItems } from '../Features/Product/ProductSlice';
import { loggeduser } from '../Authentication/AuthSlice';
import { AddToCArtAsync } from '../Features/Cart/CartSlice';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';




const sortOptions = [ 
  { name: 'Best Rating',_sort:"rating",_order:"desc" },
  { name: 'Price: Low to High', _sort:'price', _order:'asc'},
  { name: 'Price: High to Low', _sort:'price',_order:"desc" },
]


function classNames(...classes) {
  return classes.filter(Boolean).join(' ')
}

export default function Products() {
  const [page,setPage] = useState(0);
  const [sort,setSort] = useState({})
  const [quantity,setQuantity] = useState(1);
  const products = useSelector(ToatalProducts);
  const totalitem = useSelector(TotalItems);
  const user = useSelector(loggeduser);

 
  const PageSize = 10;
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const TotalPage = Math.ceil(totalitem/PageSize)

  const hanndleSort = (option) => {
    const sort ={_sort:option._sort,_order:option._order}
    setSort(sort)
  }

  const HandleCart = (id)=>{
    if(!user) {
      navigate('/login')
      return ;
    }
    dispatch(AddToCArtAsync({product:id,user:user.id,quantity:quantity}))
    toast("Product is Added TO Cart")
    

  }

  const handleClick = (data)=>{
    dispatch(FetchProductbyIdAsync({id:data._id}))
    dispatch(FetchProductbyCategoryAsync({category:data.category}))
    setTimeout(() => {navigate('/products/details')}, 50);
    
  }

  useEffect(()=>{

    const pagination = {_page:page+1,_limit:PageSize}
    
    dispatch(FetchProductAsync({sort, pagination}))
  },[dispatch,sort, page])
  return (
    <div>
      <NavBar></NavBar>
      <ToastContainer />
      <div className=" bg-light-orange-100 pb-12 px-32">
      <div>
    
        

        <main className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8 ">
          <div className="flex items-baseline  justify-end  pb-6 pt-12">
           

            <div className="flex items-center justify-end">
              <Menu as="div" className="relative inline-block text-left">
                <div>
                  <Menu.Button className="group inline-flex  text-sm font-medium text-gray-700 hover:text-gray-900 justify-end">
                    Sort
                    <ChevronDownIcon
                      className="-mr-1 ml-1 h-5 w-5 flex-shrink-0 text-gray-400 group-hover:text-gray-500"
                      aria-hidden="true"
                    />
                  </Menu.Button>
                </div>

                <Transition
                  as={Fragment}
                  enter="transition ease-out duration-100"
                  enterFrom="transform opacity-0 scale-95"
                  enterTo="transform opacity-100 scale-100"
                  leave="transition ease-in duration-75"
                  leaveFrom="transform opacity-100 scale-100"
                  leaveTo="transform opacity-0 scale-95"
                >
                  <Menu.Items className="absolute right-0 z-10 mt-2 w-40 origin-top-right rounded-md bg-white shadow-2xl ring-1 ring-black ring-opacity-5 focus:outline-none">
                    <div className="py-1">
                      {sortOptions.map((option) => (
                        <Menu.Item key={option.name}>
                          {({ active }) => (
                            <div
                            onClick={(e)=> hanndleSort(option)}
                              
                              className={classNames(
                                option.current ? 'font-medium text-gray-900' : 'text-gray-500',
                                active ? 'bg-gray-100' : '',
                                'block px-4 py-2 text-sm'
                              )}
                            >
                              {option.name}
                            </div>
                          )}
                        </Menu.Item>
                      ))}
                    </div>
                  </Menu.Items>
                </Transition>
              </Menu>

              
            </div>
          </div>

         
        </main>

        <div className='grid grid-cols-4 gap-16'>
          {products.map((data)=>{
             const arr = [];

             for(let i=0;i<data.rating;i++){
                arr.push(1);
             }
            return (
              <div>
                <img src={data.thumbnail} onClick={()=> handleClick(data)}/>
                <p className='pt-4 rufina-regular text-sm text-gray-green'>{data.title}</p>
                <div className='pt-2'>

                {
               arr.map(()=>
                  <span className='text  text-dark-orange-1000 text-xl'>&#9733; </span>
               )
            }
          <span className='charmonman-regular text-dark-orange-1000'>{data.reviews} reviews</span>
          </div>
          <div className=''>
            <span className='l line-through pr-2 text-gray-green'>&#x20B9; {data.price}</span>
            <span className='pr-2 text-gray-green sansita-regular'> &#x20B9;{Math.round(data.price - (data.price*data.discountpercent/100))}</span>
            <span className='text-dark-orange-1000 charmonman-regular text-sm'>{data.discountpercentage}% Save</span>
         </div>
         <div className="flex justify-center gap-10 pt-4">
                <button type='button' className="text text-white-shoadow px-5 py-2 shadow-2xl bg-gray-green rounded-full hover:text-gray-green hover:bg-white-shoadow  text-base sansita-regular">
              Shop Now
            </button>
            <button type='button' onClick={()=> HandleCart(data._id)} className="text hover:text-white-shoadow px-4 py-2 hover:bg-gray-green rounded-full text-gray-green  shadow-2xl bg-white-shoadow text-base sansita-regular">
              Add To Cart
            </button>
            </div>
                </div>
            )
          })}
        </div>
      </div>
    </div>
    <div className="flex py-4 items-center justify-between border-t border-gray-200 bg-white px-32 ">
      <div className="flex flex-1 justify-between sm:hidden">
        <div
          
          className="relative inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Previous
        </div>
        <div
          href="#"
          className="relative ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Next
        </div>
      </div>
      <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
        <div>
          <p className="text-sm text-gray-700">
            Showing <span className="font-medium">{PageSize*(page)+1}</span> to <span className="font-medium">{PageSize*(page+1)>totalitem?totalitem:PageSize*(page+1)}</span> of{' '}
            <span className="font-medium">{totalitem}</span> results
          </p>
        </div>
        <div>
          <nav className="isolate inline-flex -space-x-px rounded-md shadow-sm" aria-label="Pagination">
            <div
             
              className={`relative inline-flex items-center rounded-l-md px-2 py-2 ${page+1 === 1? 'hidden':'block'} text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0 cursor-pointer`}
              onClick={()=> setPage(page-1)}
            >
              <span className="sr-only">Previous</span>
              <ChevronLeftIcon className="h-5 w-5" aria-hidden="true" />
            </div>
            

            {
              Array.from({length:TotalPage}).map((el,index)=>{
                return (
                  <div
              
              aria-current="page"
              className={`relative cursor-pointer z-10 inline-flex items-center ${page+1 === index+1 ? 'bg-gray-green text-white': 'bg-white text-gray-green'} px-4 py-2 text-sm font-semibold    focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600`}
              onClick={()=> setPage(index)}
            >
              {index+1}
            </div>
                  
                )
              })
            }
           
            <div
             
              className={`relative inline-flex items-center rounded-r-md px-2 py-2 ${page+1 === TotalPage? 'hidden':'block'} text-gray-400 ring-1 ring-inset ring-gray-300 cursor-pointer hover:bg-gray-50 focus:z-20 focus:outline-offset-0`}
              onClick={()=>setPage(page+1)}
            >
              <span className="sr-only">Next</span>
              <ChevronRightIcon className="h-5 w-5" aria-hidden="true" />
            </div>
          </nav>
        </div>
      </div>
    </div>
    </div>
  )
}
