export async function CreateOrder(userdata){
   return new Promise(async (resolve )=>{
      const response = await fetch('/order/add',{
         method:"POST",
         body:JSON.stringify(userdata),
         headers:{"content-type":"application/json"}
      })
      const data = response.json();
      resolve({data})
   })
}

