import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { FetchProduct, FetchProductbyCategory, FetchProductbyID } from "./ProductAPI";
const initialState = {
  status: "idle",
  products: [],
  product: [],
  similarproduct: [],
  totalitems: 0,
};

export const FetchProductAsync = createAsyncThunk(
  "Product/FetchProduct",
  async ({ sort, pagination }) => {
    const response = await FetchProduct(sort, pagination);
    return response.data;
  }
);

export const FetchProductbyIdAsync = createAsyncThunk(
  "Product/FetchProductbyID",
  async (userdata) => {
    const response = await FetchProductbyID(userdata);
    return response.data;
  }
);

export const FetchProductbyCategoryAsync = createAsyncThunk(
  "Product/FetchProductbyCategory",
  async (userdata) => {
    const response = await FetchProductbyCategory(userdata);
    return response.data;
  }
);

export const ProductSlice = createSlice({
  name: "Product",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(FetchProductAsync.pending, (state) => {
        state.status = "pending";
      })
      .addCase(FetchProductAsync.fulfilled, (state, actions) => {
        state.status = "idle";
        state.products = actions.payload.products;
        state.totalitems = actions.payload.totalitems;
      })
      .addCase(FetchProductbyIdAsync.pending,(state)=>{
        state.status = "pending";
      })
      .addCase(FetchProductbyIdAsync.fulfilled,(state,actions)=>{
        state.status = 'ilde';
        state.product=actions.payload;
      })
      .addCase(FetchProductbyCategoryAsync.pending,(state)=>{
        state.status = "pending";
      })
      .addCase(FetchProductbyCategoryAsync.fulfilled,(state,actions)=>{
        state.status = 'idle';
        state.similarproduct = actions.payload;
      });
  },
});

export const ToatalProducts = (state) => state.Product.products;
export const TotalItems = (state) => state.Product.totalitems;
export const SelectedProduct = (state) => state.Product.product;
export const selectsimilarProduct = (state)=> state.Product.similarproduct;

export default ProductSlice.reducer;
