import React, { useState } from 'react'
import NavBar from '../../../Naviagations/NavBar'
import { useDispatch, useSelector } from 'react-redux'
import { Address } from '../../Address/AddressSlice'
import { loggeduser } from '../../../Authentication/AuthSlice';
import { CartData } from '../../Cart/CartSlice';
import { CreateOrderAsync } from '../OrderSlice';
import {loadStripe} from '@stripe/stripe-js';

export default function Order() {
   const dispatch = useDispatch()
   const [Addresses,setAddress] = useState(0);
   const [Payment,SetPayment] =useState("")
   const date = new Date();
   const dilevry_date = new Date();
   dilevry_date.setDate(date.getDate() + 10);
   const user = useSelector(loggeduser)
   const products = useSelector(CartData)
   const address = useSelector(Address)
   let totalitem= 0;
   let totalprice = 0;
   const Product_id = []

  
   for(let i=0;i<products.length;i++){
      totalitem = totalitem + products[i].quantity;
      totalprice = totalprice + Math.round(products[i].product.price/100*products[i].product.discountpercent)*products[0].quantity;
      Product_id.push({id:products[i].product._id,title:products[i].product.title,quantity:products[i].quantity})
    }


    const Create_paymet = async ()=>{
      dispatch(CreateOrderAsync({
        quantity:totalitem,
        user:user.id,
        product:Product_id,
        Address:address[Addresses]._id,
        Order_date:date,
        Dilverey_date:dilevry_date,
        payment_method:Payment,
     }))
   
    
      const stripe = await loadStripe("pk_test_51NNa7MSDHK09cQh7prrFvWdcVSBJouLqdW6i1IjvVOVDaGsbu40y1paM7NGnxP930pxzlTylMK6ZebHXB6ncSLIY00DABWyl9y")
  
      const body = {
        products:Product_id,
        Price:totalprice,


      }
      const response = await fetch('//create-checkout-sesssion',{
        method:"POST",
        body:JSON.stringify(body),
        headers:{'content-type':"application/json"}
      })
      const sesssion = await response.json();
   
      const result = stripe.redirectToCheckout({
        sessionId:sesssion.id
      })
      if(result.error){
        console.log(result.error)
      }
    }
  
   

    const HandleOrder = ()=>{
      if(!Product_id && Addresses){
        alert("Please Fill Details");
        return ;
      }
      if(Payment == "Card"){
        Create_paymet()
        return ;

      }
      dispatch(CreateOrderAsync({
         quantity:totalitem,
         user:user.id,
         product:Product_id,
         Address:address[Addresses]._id,
         Order_date:date,
         Dilverey_date:dilevry_date,
         payment_method:Payment,
      }))

    }
  return (
   <div>
      <NavBar/>
      <div className='px-60'>
   <form>
   <div className="space-y-12">
     
     

     <div className="border-b border-gray-900/10 pb-12">
       <h2 className="text-base font-semibold leading-7 text-gray-900">Choose Existing Address</h2>
     
       <div className="mt-10 space-y-10">
         <fieldset>
           <legend className="text-sm font-semibold leading-6 text-gray-900">Addresses</legend>
           <div className="mt-6 space-y-6">
             {
               address.map((item,index)=>{
                  return (
                     <div className="relative flex gap-x-3">
               <div className="flex h-6 items-center">
                 <input
                 onClick={()=>setAddress(index)}
                   id="offers"
                   name="offers"
                   type="checkbox"
                   className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-600"
                 />
               </div>
               <div className='grid grid-cols-3 mb-16 py-12 bg-light-orange-500'>
      
     
      
      <div className=' flex flex-col  px-8 pt-4'>
      <div >

            <span className="text-gray-green sansita-regular text-base pr-1">Name:-</span>
            <span className='text-dark-red sansita-regular'>{item.user.username}</span>
         </div>
         <div className='pt-2 '>
            <span className="text-gray-green sansita-regular text-sm pr-1">Primary Contact Number :-</span>
            <span className='text-dark-red sansita-regular'>{item.primary_contact_number}</span>
         </div>
         <div className='pt-2'>
            <span className="text-gray-green sansita-regular text-sm pr-1 ">Secondary Contact Number:- </span>
            <span className='text-dark-red sansita-regular'>{item.Secondary_Contact_Number}</span>
         </div>

      </div>
      <div className=' flex flex-col items-end px-8 pt-4'>
      <div >

            <span className="text-gray-green pt-1 sansita-regular text-base pr-1">Street:-</span>
            <span className='text-dark-red sansita-regular'>{item.street}</span>
         </div>
         <div className='pt-2 '>
            <span className="text-gray-green pt-1 sansita-regular text-sm pr-1">District:-</span>
            <span className='text-dark-red sansita-regular'>{item.District}</span>
         </div>
         <div className='pt-2'>
            <span className="text-gray-green pt-1 sansita-regular text-sm pr-1 ">State:- </span>
            <span className='text-dark-red sansita-regular'>{item.State}</span>
         </div>
         <div className='pt-2'>
            <span className="text-gray-green pt-1 sansita-regular text-sm pr-1 ">ZipCode:- </span>
            <span className='text-dark-red sansita-regular'>{item.Zipcode}</span>
         </div>
         

      </div>
     
      
    </div>
             </div>

                  )
               })
             }
              
            
             
           </div>
         </fieldset>
         <fieldset>
           <legend className="text-sm font-semibold leading-6 text-gray-900">Choose Payment Method</legend>
          
           <div className="mt-6 space-y-6">
             <div className="flex items-center gap-x-3">
               <input
               onClick={()=>SetPayment("COD")}
                 id="push-everything"
                 name="push-notifications"
                 type="radio"
                 className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
               />
               <label htmlFor="push-everything" className="block text-sm font-medium leading-6 text-gray-900">
                 Cash On Dilevery
               </label>
             </div>
             <div className="flex items-center gap-x-3">
               <input
               onClick={()=>SetPayment("Card")}
                 id="push-email"
                 name="push-notifications"
                 type="radio"
                 className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
               />
               <label htmlFor="push-email" className="block text-sm font-medium leading-6 text-gray-900">
                 Card Payment
               </label>
             </div>
             
           </div>
         </fieldset>
       </div>
     </div>
   </div>

   <div className="mt-6 flex items-center justify-end gap-x-6">
     <button type="button" className="text-sm font-semibold leading-6 text-gray-900">
       Cancel
     </button>
     <button
       type='button'
       onClick={()=>HandleOrder()}
       className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
     >
      Order
     </button>
   </div>
 </form>
 </div>
 </div>
  )
}
